    export default {
        replace:true,
        data (){
            return {
                show: false,
            }
        },
        ready (){
            $(window).on('scroll', () => {
                if($(window).scrollTop() > 100){
                    this.show = true;
                }
            });
        },
        beforeDestory (){
            $(window).off('scroll');
        },
        methods:{
            goTop (){
                $(window).scrollTop(0);
                this.show = false;
            }
        }
    }
